@font-face {
  font-family: "BebasNeueRegular";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/BebasNeue-Regular.woff2") format("woff2"), url("../fonts/BebasNeue-Regular.woff") format("woff"); }

@font-face {
  font-family: "BebasNeueBold";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/BebasNeue-Bold.woff2") format("woff2"), url("../fonts/BebasNeue-Bold.woff") format("woff"); }

@font-face {
  font-family: "RobotoCondensed";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Roboto-Condensed.woff2") format("woff2"), url("../fonts/Roboto-Condensed.woff") format("woff"); }

@font-face {
  font-family: "RobotoCondensedLight";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Light.woff2") format("woff2"), url("../fonts/RobotoCondensed-Light.woff") format("woff"); }

@font-face {
  font-family: "RobotoCondensedBold";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff"); }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  padding: 0;
  margin: 0;
  font-family: "RobotoCondensed", "Arial Narrow", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: #102654;
  background-color: white;
  min-width: 320px; }

a:not(:first-child) {
  color: inherit;
  text-decoration: none; }
  a:not(:first-child):focus, a:not(:first-child):hover {
    opacity: 0.5; }
  a:not(:first-child):active {
    opacity: 0.5; }

.button {
  display: block;
  text-transform: uppercase;
  text-align: center;
  box-sizing: border-box; }
  .button:focus, .button:hover {
    opacity: 0.5; }
  .button:active {
    opacity: 0.5; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0); }

@media (min-width: 1200px) {
  body {
    background: url("../img/bg-desktop.png") no-repeat 50% 0;
    background-size: 1366px auto;
    min-height: 5889px; } }

@media (min-width: 768px) and (max-width: 1199px) {
  body {
    background: url("../img/bg-tablet.png") no-repeat 50% 0;
    background-size: 768px auto; } }

@media (min-width: 320px) and (max-width: 767px) {
  body {
    background: url("../img/bg-mobile.png") no-repeat 50% 0;
    background-size: 320px auto; } }

.page-header__container {
  max-width: 1160px;
  margin: 0 auto; }

.page-header__container-logo-and-mobile {
  padding-top: 30px;
  display: flex;
  justify-content: space-between; }

.page-header__container-about {
  display: flex;
  justify-content: space-between; }

.page-header__wrapper-text {
  width: 34.2%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-top: auto;
  border: 2px solid black; }
  .page-header__wrapper-text ul {
    margin: 0;
    padding: 0;
    list-style: none;
    border: none;
    text-align: right; }

.page-header__wrapper-img {
  width: 52.5%;
  border: 2px solid black; }
  .page-header__wrapper-img img {
    width: 500px;
    height: 556px; }

.page-header__wrapper-links {
  width: 2%;
  border: 2px solid black; }
  .page-header__wrapper-links ul {
    margin: 0;
    padding: 0;
    list-style: none;
    border: none; }

@media (min-width: 768px) and (max-width: 1199px) {
  .page-header__container {
    max-width: 100%;
    margin: 0 auto;
    padding: 0 45px; }
  .page-header__container-logo-and-mobile {
    padding-top: 40px; }
  .page-header__wrapper-text {
    width: 48.2%; }
  .page-header__wrapper-img {
    text-align: right;
    width: 48.4%; }
    .page-header__wrapper-img img {
      width: 324px;
      height: 390px; }
  .page-header__wrapper-links {
    display: none; } }

@media (min-width: 320px) and (max-width: 767px) {
  .page-header__container {
    max-width: 100%;
    margin: 0 auto;
    padding: 0 15px; }
  .page-header__container-logo-and-mobile {
    padding-top: 20px; }
  .page-header__wrapper-text {
    width: 100%; }
  .page-header__wrapper-img {
    display: none; }
  .page-header__wrapper-links {
    display: none; } }
